import { AnagramaGame, SavedGame } from '../../interfaces'

export function GetSavedGame(anagramaNormalizado: string): SavedGame | null {
  const saved = localStorage.getItem('saved:' + anagramaNormalizado)
  if (saved) {
    return JSON.parse(saved) as SavedGame
  }
  return null
}

export function GetLatestSavedGameAnagram(): string | null {
  const list = GetOrderedSave()
  if (list.length === 0) return null
  return list[list.length - 1].anagram
}

export interface SavedMetadata {
  saved: boolean
  solved: boolean
}

export function IsSaved(anagram: string): SavedMetadata {
  const s = GetSavedGame(anagram)
  if (s) {
    return {
      saved: true,
      solved: s.solved,
    }
  }
  return { saved: false, solved: false }
}

export function GetAllSavedGames(): SavedGame[] {
  return Object.keys(localStorage)
    .filter((value) => value.startsWith('saved'))
    .map(
      (value) => JSON.parse(localStorage.getItem(value) as string) as SavedGame
    )
}

export function GetOrderedSave(): SavedGame[] {
  return GetAllSavedGames().sort((a, b) => a.updated.localeCompare(b.updated))
}

export function SaveGame(anagramaGame: AnagramaGame): void {
  const saved = GetSavedGame(anagramaGame.word.anagram)
  if (saved) {
    saved.game = anagramaGame
    saved.updated = new Date().toJSON()
    saved.solved =
      anagramaGame.solutions.find((solution) => !solution.solved) === undefined
    localStorage.setItem(
      'saved:' + anagramaGame.word.anagram,
      JSON.stringify(saved)
    )
  } else {
    localStorage.setItem(
      'saved:' + anagramaGame.word.anagram,
      JSON.stringify({
        anagram: anagramaGame.word.anagram,
        created: new Date().toJSON(),
        updated: new Date().toJSON(),
        game: anagramaGame,
        solved:
          anagramaGame.solutions.find((solution) => !solution.solved) ===
          undefined,
      } as SavedGame)
    )
  }
}
