import React from 'react'
import { Container } from 'react-bootstrap'
import { HashRouter as Router, Switch, Route } from 'react-router-dom'
import './App.css'
import { GameListComponent } from './components/game-list'
import { GameLoaderComponent } from './components/game-loader'
import { LatestGameComponent } from './components/latest-game'
import { LatestSaveComponent } from './components/latest-save'
import { MainComponent } from './components/main'
import { TopMenuComponent } from './components/top-menu'

function App() {
  return (
    <Router>
      <TopMenuComponent />
      <Container>
        <Switch>
          <Route path="/" exact>
            <MainComponent />
          </Route>
          <Route path="/game/continue" exact>
            <LatestSaveComponent />
          </Route>
          <Route path="/game/list" exact>
            <GameListComponent />
          </Route>
          <Route path="/game/latest" exact>
            <LatestGameComponent />
          </Route>
          <Route path="/game/:anagrama">
            <GameLoaderComponent />
          </Route>
        </Switch>
      </Container>
    </Router>
  )
}

export default App
