import React from 'react'
import { Button } from 'react-bootstrap'
import { useDispatch, useSelector } from 'react-redux'
import {
  clearLetters,
  SaveGameThunk,
  selectAnagramaWord,
  shuffleLetters,
} from '../features/anagramaGame/anagramaGameSlice'

import { AnagramaLetterComponent } from './letter'

export function AnagramaWordComponent() {
  const dispatch = useDispatch()
  const word = useSelector(selectAnagramaWord)
  const { currentWord, letters } = word
  const handleShuffle = () => {
    dispatch(shuffleLetters())
    dispatch(SaveGameThunk())
  }
  const handleClear = () => {
    dispatch(clearLetters())
    dispatch(SaveGameThunk())
  }
  return (
    <div>
      <div className="row">
        <div className="col-12 text-center">
          {letters.map((letter) => {
            return (
              <AnagramaLetterComponent
                key={letter.letter + letter.index}
                {...letter}
              />
            )
          })}
          <Button className="btn-warning mt-2" onClick={handleShuffle}>
            Misturar
          </Button>
        </div>
      </div>
      <div className="row justify-content-md-center mt-2">
        <div className="col-12 col-md-6 text-center h2 alert alert-info">
          {currentWord}{' '}
          <Button
            className="btn-danger float-right"
            onClick={handleClear}
            disabled={word.currentWord === ''}
          >
            Limpar
          </Button>
        </div>
      </div>
    </div>
  )
}
