import React, { useCallback, useEffect } from 'react'
import { useDispatch } from 'react-redux'
import {
  clearLetters,
  SaveGameThunk,
  selectLetter,
} from '../features/anagramaGame/anagramaGameSlice'
import { AnagramaSolutionsComponent } from './solutions'
import { AnagramaWordComponent } from './word'

export function AnagramaGameComponent() {
  const dispatch = useDispatch()
  const handleKeyDown = useCallback(
    (evt: KeyboardEvent) => {
      if (evt.key.length === 1) {
        const letter = evt.key
          .normalize('NFD')
          .replace(/[\u0300-\u036f]/g, '')
          .toUpperCase()
        dispatch(selectLetter({ letter }))
        dispatch(SaveGameThunk())
      } else if (evt.key === 'Backspace') {
        dispatch(clearLetters())
        dispatch(SaveGameThunk())
      }
    },
    [dispatch]
  )
  useEffect(() => {
    window.addEventListener('keydown', handleKeyDown)
    return () => {
      window.removeEventListener('keydown', handleKeyDown)
    }
  }, [handleKeyDown])
  return (
    <div className="mt-2">
      <AnagramaWordComponent />
      <AnagramaSolutionsComponent />
    </div>
  )
}
