import React from 'react'
import { Button } from 'react-bootstrap'
import { useDispatch } from 'react-redux'
import {
  SaveGameThunk,
  selectLetter,
} from '../features/anagramaGame/anagramaGameSlice'
import { AnagramaLetter } from '../interfaces'

export function AnagramaLetterComponent(props: AnagramaLetter) {
  const dispatch = useDispatch()

  const { letter, index, used } = props
  const handleClick = () => {
    dispatch(selectLetter({ letter, index }))
    dispatch(SaveGameThunk())
  }

  return (
    <Button
      className="mx-1 mt-2 pd-0 btn-lg shadow"
      style={{
        width: '2em',
        height: '2em',
        lineHeight: '2em',
        textAlign: 'center',
        verticalAlign: 'middle',
        padding: '0',
        fontSize: '2em',
      }}
      disabled={used}
      onClick={handleClick}
    >
      {letter.toUpperCase()}
    </Button>
  )
}
