import { useEffect } from 'react'
import { useState } from 'react'
import { Button } from 'react-bootstrap'
import { NavLink } from 'react-router-dom'
import {
  AnagramaMetaData,
  getRachacucaAnagramGamesInOrder,
} from '../features/anagramaGame/AnagramAPI'
import ClassNames from 'classnames'

export function GameListComponent() {
  const [anagramas, setAnagramas] = useState<AnagramaMetaData[] | null>(null)
  const [error, setError] = useState<string | null>(null)
  useEffect(() => {
    ;(async () => {
      try {
        setAnagramas(await getRachacucaAnagramGamesInOrder())
      } catch (e) {
        setError('Não foi possível carregar a lista de jogos.')
      }
    })()
  }, [])
  if (error) {
    return <div>{error}</div>
  }
  if (anagramas) {
    return (
      <div className="row">
        {anagramas.map((value) => {
          return (
            <Button
              className={ClassNames([
                'btn-lg btn-block',
                {
                  'btn-primary': value.saved,
                  'btn-secondary': !value.saved,
                  'btn-success': value.solved,
                },
              ])}
              as={NavLink}
              to={`/game/${value.anagrama}`}
              key={value.anagrama}
            >
              {value.anagrama.toUpperCase()}
            </Button>
          )
        })}
      </div>
    )
  }
  return <div>Carregando...</div>
}
