import React from 'react'
import ClassNames from 'classnames'
import { AnagramaSolution } from '../interfaces'

export function AnagramaSolutionComponent(props: AnagramaSolution) {
  const { solution, solved } = props

  return (
    <div
      className={ClassNames([
        //'col-md-4',
        'mx-1',
        { 'font-weight-bold': solved },
      ])}
      style={{ lineBreak: 'loose', maxWidth: '10em', display: 'block' }}
    >
      {solved ? solution.toUpperCase() : '?'.repeat(solution.length)}
    </div>
  )
}
