import React, { useState } from 'react'
import { useEffect } from 'react'
import { Redirect } from 'react-router-dom'
import { GetLatestSavedGameAnagram } from '../features/anagramaGame/SavedGame'

export function LatestSaveComponent() {
  const [anagrama, setAnagrama] = useState<string | null>(null)
  const [error, setError] = useState<string | null>(null)
  useEffect(() => {
    const latestSaveAnagram = GetLatestSavedGameAnagram()
    if (latestSaveAnagram === null) {
      setError('Não há jogos salvos!')
    } else {
      setAnagrama(GetLatestSavedGameAnagram())
    }
  }, [])

  if (error != null) {
    return <Redirect to={`/`} />
  }

  if (anagrama != null) {
    return <Redirect to={`/game/${anagrama}`} />
  }

  return <div>carregando...</div>
}
