import React, { useEffect, useState } from 'react'
import { useDispatch } from 'react-redux'
import { useParams } from 'react-router-dom'
import { setAnagramaData } from '../features/anagramaGame/anagramaGameSlice'
import {
  convertRachacucaToAnagramaGame,
  getRachacucaAnagrama,
} from '../features/anagramaGame/AnagramAPI'
import { GetSavedGame } from '../features/anagramaGame/SavedGame'
import { AnagramaGameComponent } from './game'

interface GameLoaderParams {
  anagrama: string
}

export function GameLoaderComponent() {
  const dispatch = useDispatch()
  const { anagrama } = useParams() as GameLoaderParams
  const [loaded, setLoaded] = useState<boolean>(false)
  const [error, setError] = useState<string | null>(null)
  useEffect(() => {
    ;(async () => {
      const normalizedAnagrama = anagrama.split('').sort().join('')
      const saved = GetSavedGame(normalizedAnagrama)
      if (saved) {
        dispatch(setAnagramaData(saved.game))
        setLoaded(true)
      } else {
        try {
          const gameData = convertRachacucaToAnagramaGame(
            await getRachacucaAnagrama(normalizedAnagrama)
          )
          dispatch(setAnagramaData(gameData))
          setLoaded(true)
        } catch (e) {
          setError('Os dados deste jogo não puderam ser carregados.')
        }
      }
    })()
  }, [anagrama, dispatch])

  if (error !== null) {
    return <div>{error}</div>
  }
  if (loaded) {
    return (
      <div>
        <AnagramaGameComponent />
      </div>
    )
  }
  return <div>Carregando: {anagrama}</div>
}
