import React from 'react'
import { useSelector } from 'react-redux'
import { selectAnagramaSolutions } from '../features/anagramaGame/anagramaGameSlice'
import { AnagramaSolutionComponent } from './solution'
export function AnagramaSolutionsComponent() {
  const solutions = useSelector(selectAnagramaSolutions)
  return (
    <div className="card">
      <div className=" card-body">
        <div
          className="row d-flex align-items-start text-center flex-column"
          style={{
            minHeight: '5em',
            maxHeight: 'calc(100vh - 25rem)',
            overflow: 'auto',
          }}
        >
          {solutions.map((solution) => {
            return (
              <AnagramaSolutionComponent
                key={solution.solution}
                {...solution}
              />
            )
          })}
        </div>
      </div>
    </div>
  )
}
