import React, { useState } from 'react'
import { useEffect } from 'react'
import { Redirect } from 'react-router-dom'
import { getLastRachacucaGame } from '../features/anagramaGame/AnagramAPI'

export function LatestGameComponent() {
  const [anagrama, setAnagrama] = useState<string | null>(null)
  const [error, setError] = useState<string | null>(null)
  useEffect(() => {
    ;(async () => {
      try {
        setAnagrama(await getLastRachacucaGame())
      } catch (e) {
        setError('Ocorreu um erro!')
      }
    })()
  }, [])

  if (error != null) {
    return <Redirect to={`/`} />
  }

  if (anagrama != null) {
    return <Redirect to={`/game/${anagrama}`} />
  }

  return <div>carregando...</div>
}
